.pivot-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 50% 50%;
  /* border: 2px black solid; */
  /* border: 2px solid; */
}

.overlay {
  position: relative;
  display: inline-block;
  /* background: cyan; */
  display: inline-block;
  width: 100%;
  height: 85%;
}

.overlay > .pivot-img {
  vertical-align: middle;
}

.overlay > .jonah-img {
  vertical-align: middle;
}

.overlay::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0;
  transition: 0.5s ease;
}

.overlay:hover::before {
  opacity: 0.23;
}

.pivot-img:hover {
  opacity: 0.5;
  background-color: black;
}

.playroll-img {
  width: 100%;
  height: 85%;
  object-fit: cover;
  object-position: 0% 50%;
}

